<template>
  <div class="warpper">
    <div class="header">
      <span>导入学生</span>
      <div class="search_wrap">
        <span class="impot_title">班级：</span>
        <el-select v-model="classId" filterable placeholder="请选择班级">
          <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value"></el-option>
        </el-select>
      </div>
    </div>
    <div class="content" v-if="classId">
      <table class="tableWrap">
        <thead>
          <tr>
            <th>序号</th>
            <th>学生姓名</th>
            <th>性别</th>
            <th>学号</th>
            <th>手机号</th>
            <!-- <th>是否住宿</th> -->
            <th>编辑</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(item, index) in list" :key="index" @click="tag = index">
            <td>{{index+1}}</td>
            <td>
              <input type="text" class="input_set" @paste="cellkeydown" v-model.trim="item.studentName" :placeholder="index === 0 ? '必填（输入或直接粘贴）' : ''">
            </td>
            <td>
              <input type="text" class="input_set" v-model.trim="item.studentSex" :placeholder="index === 0 ? '选填（男或女）': ''">
            </td>
            <td>
              <input type="text" class="input_set" v-model.trim="item.studentNumber" :placeholder="index === 0 ? '选填': ''">
            </td>
            <td>
              <input type="number" class="input_set" v-model.trim="item.studentTelephone_num" :placeholder="index === 0 ? '选填': ''">
            </td>
            <!-- <td>
              <input type="text" v-model="item.isLodging" :placeholder="index === 0 ? '选填（是或否）': ''">
            </td> -->
            <td class="delete_icon">
              <i v-if="!showDelete" class="el-icon-error change_delete_color" @click="deleteList(index)"></i>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
    <div v-else class="empty_wrap">
      <div class="prop_center">
        <img src="../../assets/empty.png">
        <p>暂无数据</p>
      </div>
    </div>
    <div class="bottom_button" v-if="classId">
      <span class="impot_button" @click="cleatData" >清空</span>
      <span class="impot_button" @click="addData" >新增</span>
      <span class="impot_button" @click="submitData" >导入</span>
    </div>
  </div>
</template>
<script>
  import baseApi from '../api'
  import importApi from '../student/api'
  export default {
    data () {
      return {
        options: [],
        classId: null,
        showDelete: false,
        list: [
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          }
        ]
      }
    },
    mounted () {
      baseApi.getClassList({schoolId: this.$route.params.schoolId, classType: 1, rowStart: 0, rowCount: 999999}, (data) => {
        data.forEach((element) => {
          var item = {
            value: element.classId,
            label: element.className
          }
          this.options.push(item)
        })
      }, (er) => {})
    },
    methods: {
      submitData () {
        if (this.classId !== undefined && this.classId !== '') {
          var nameList = []
          for (let i = 0; i < this.list.length; i++) {
            if (this.list[i].studentName !== '') {
              nameList.push(this.list[i].studentName)
            }
            if (this.list[i].studentSex !== undefined && this.list[i].studentSex !== null && this.list[i].studentSex !== '') {
              if (this.list[i].studentSex !== '男' && this.list[i].studentSex !== '女') {
                this.$notify.error({
                  title: '提示',
                  message: '请输入男或女！'
                })
                return
              }
            }
            if (this.list[i].studentTelephone_num) {
              if (!(/^1[34578]\d{9}$/.test(this.list[i].studentTelephone_num))) {
                this.$notify.error({
                  title: '提示',
                  message: '请填写正确手机号码！'
                })
                return
              }
            }
          }
          this.checkRepeat(nameList)
        }
      },
      checkRepeat (arr) {
        var repeatList = this.removeDuplicatedItem(arr)
        if (repeatList.length > 0) {
          this.$alert('有重复学生姓名: ' + repeatList.join('、') + '。请检查修改后导入！', '提示', {
            confirmButtonText: '确定'
          })
        } else {
          this.importData()
        }
      },
      removeDuplicatedItem (arr) {
        var newArr = arr.sort()
        var repeatList = []
        var ret = []
        for (var a = 0; a < newArr.length - 1; a++) {
          if (newArr[a] === newArr[a + 1]) {
            repeatList.push(newArr[a])
          }
        }
        for (var i = 0, j = repeatList.length; i < j; i++) {
          if (ret.indexOf(repeatList[i]) === -1) {
            ret.push(repeatList[i])
          }
        }
        return ret
      },
      importData () {
        var finishData = []
        for (let i = 0; i < this.list.length; i++) {
          if (this.list[i].studentName !== '') {
            var item = {
              studentName: this.list[i].studentName,
              studentSex: this.list[i].studentSex,
              studentNumber: this.list[i].studentNumber,
              studentTelephone_num: this.list[i].studentTelephone_num
            }
            finishData.push(item)
          }
        }
        if (finishData.length > 150) {
          this.$message.error('一次最多导入150位学生')
          return
        }
        importApi.importStudent({schoolId: this.$route.params.schoolId, classId: this.classId, dataList: finishData}, (data) => {
          this.$alert('导入成功！', '提示', {
            confirmButtonText: '确定'
          })
          this.cleatData()
        }, (er) => {
          if (er.toUser !== undefined && er.toUser) {
            this.$message.error(er.message)
          } else {
            this.$message.error('导入失败')
          }
        })
      },
      addData () {
        this.list.push(
          {
            studentName: '',
            studentSex: '',
            studentNumber: '',
            studentTelephone_num: ''
          }
        )
      },
      deleteList (index) {
        this.list.splice(index, 1)
      },
      cellkeydown (event) {
        if (event.type === 'paste') {
          var content = document.querySelector('#textAreaXXDD')
          content.value = ''
          content.focus()
          var inputs = document.querySelectorAll('input')
          for (let i = 0; i < inputs.length; i++) {
            inputs[i].blur()
          }
          setTimeout(() => {
            this.fillData()
          }, 100)
        }
      },
      fillData () {
        var content = document.querySelector('#textAreaXXDD')
        var len = content.value.split('\n')
        var copyData = []
        for (let i = 0; i < len.length; i++) {
          var item = {
            studentName: len[i].split(/\s+/)[0],
            studentSex: len[i].split(/\s+/)[1],
            studentNumber: len[i].split(/\s+/)[2],
            studentTelephone_num: len[i].split(/\s+/)[3]
            // isLodging: len[i].split(/\s+/)[4]
          }
          copyData.push(item)
        }
        if (this.tag !== null && this.tag !== undefined) {
          this.list = this.list.splice(0, this.tag).concat(copyData)
        } else {
          this.list = copyData
        }
      },
      cleatData () {
        this.list = [
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          },
          {
            studentName: '',
            studentSex: '',
            studentTelephone_num: '',
            studentNumber: '',
            isLodging: ''
          }
        ]
      }
    }
  }
</script>
<style lang="less" scoped>
@import url('../../style/public.less');
.warpper {
  .header {
    .search_wrap {
      float: right;
      .impot_title {
        line-height: 60px;
        font-size: 16px;
        font-weight: 600;
      }
    }
  }
  .content {
    margin-top: 5px;
    background: #fff;
    .tableWrap{
      width: 100%;
      border-spacing: 0;
      thead tr th{
        height: 40px;
        text-align: center;
        background: #F9F9F9;
        vertical-align: middle;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        border: 1px solid #E8ECEF;
        display:table-cell;
      }
      thead tr th:nth-of-type(1){
        border-width:0px 0px 1px 1px;
        width: 5%;
      }
      thead tr th:nth-of-type(2){
        border-width:0px 0px 1px 1px;
        width: 18%;
      }
      thead tr th:nth-of-type(3){
        border-width:0px 0px 1px 1px;
        width: 18%;
      }
      thead tr th:nth-of-type(4){
        border-width:0px 0px 1px 1px;
        width: 18%;
      }
      thead tr th:nth-of-type(5){
        border-width:0px 0px 1px 1px;
        width: 18%;
      }
      thead tr th:nth-of-type(6){
        border-width:0px 0px 1px 1px;
        width: 18%;
      }
      thead tr th:nth-of-type(7){
        border-width:0px 1px 1px 1px;
        width: 5%;
      }
      tbody tr td{
        height: 40px;
        text-align: center;
        background: #fff;
        vertical-align: middle;
        font-size: 14px;
        color: #333333;
        letter-spacing: 0;
        border: 1px solid #E8ECEF;
        display:table-cell;
        table-layout:fixed;
        input{
          width: 100%;
          height: 100%;
          border: none;
          padding-left: 10px;
          box-sizing: border-box;
          outline: none;
          font-size: 16px;
        }
        .input_set::-webkit-input-placeholder {
          color:#b6bece;
          font-size:14px;
        }
        .input_set:-moz-placeholder {
          color:#b6bece;
          font-size:14px;
        }
        .input_set::-moz-placeholder {
          color:#b6bece;
          font-size:14px;
        }
        .input_set:-ms-input-placeholder {
          color:#b6bece;
          font-size:14px;
        }
      }
      tbody tr td:nth-of-type(1){
        border-width:0px 0px 1px 1px;
        width: 8%;
      }
      tbody tr td:nth-of-type(2){
        border-width:0px 0px 1px 1px;
        width: 21%;
      }
      tbody tr td:nth-of-type(3){
        border-width:0px 0px 1px 1px;
        width: 21%;
      }
      tbody tr td:nth-of-type(4){
        border-width:0px 0px 1px 1px;
        width: 21%;
      }
      tbody tr td:nth-of-type(5){
        border-width:0px 0px 1px 1px;
        width: 21%;
      }
      tbody tr td:nth-of-type(6){
        border-width:0px 1px 1px 1px;
        width: 8%;
      }
      tbody tr .delete_icon .change_delete_color{
        color: #c7c5c5;
        font-size: 25px;
        cursor: pointer;
      }
      tbody tr .delete_icon .el-icon-error:hover{
        color: #67c23a;
      }
    }
  }
  .bottom_button {
    background: #fff;
    padding: 10px 0;
    margin-top: 5px;
    .impot_button {
      display: inline-block;
      width: 100px;
      height: 36px;
      text-align: center;
      background: #67c23a;
      line-height: 36px;
      color: #fff;
      border-radius: 4px;
      cursor: pointer;
      margin-left: 15px;
      box-sizing: border-box;
    }
    .impot_button:hover {
      background: #85ce61;
      border-color: #85ce61;
      color: #fff;
    }
    .impot_button:active {
      background: #5daf34;
      border-color: #5daf34;
      color: #fff;
    }
  }
  .empty_wrap {
    height:calc(100% - 69px);
    margin-top: 5px;
    background: #fff;
    position: relative;
    .prop_center {
      width: 280px;
      text-align: center;
      position: absolute;
      left: 50%;
      top: 50%;
      -webkit-transform:translate(-50%,-50%);
      -moz-transform:translate(-50%,-50%);
      -o-transform:translate(-50%,-50%);
      transform:translate(-50%,-50%);
      img {
        width:158px;
        height:123px;
      }
      p {
        font-size: 14px;
        margin-top: 20px;
        font-weight: 700;
      }
    }
  }
}
</style>




